* {
  font-family: 'Roboto';
}

.switch-wrapper {
  position: relative;
}
.switch-wrapper > div {
  position: absolute;
  width: 100%;
}

.skill-icon {
  margin: 7px;
  cursor: pointer;
  opacity: 0.7;
  filter: alpha(opacity=70);
  position: relative;
  top: 0px;
  transition: transform .2s ease-in-out;
}
.skill-icon:hover {
  opacity: 1.0;
  filter: alpha(opacity=100); /* For IE8 and earlier */
  transform: translate3d(0px, -10px, 0px);
}

.header-link {
  opacity: 0.7;
  filter: alpha(opacity=70);
  transition: opacity 0.33s ease-in-out;
}
.header-link:hover {
  opacity: 1.0;
  font-size: 1.6rem;
  filter: alpha(opacity=100); /* For IE8 and earlier */
  transition: opacity .15s ease-in-out, font-size 0.15s ease-in-out;
}

.item-enter {
  opacity: 0;
}
.item-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.item-exit {
  opacity: 1;
}
.item-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}